.header {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 100px;

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }
}

.header__logo img {
  width: 206px;
  height: 55px;
}
