.feedback {
  padding-top: 100px;
  margin-bottom: 97px;
}

.feedback__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 50px;

  border: 1px solid $color-solitude;
  border-radius: 10px;
}

.feedback__form {
  width: 70.5%;

  form {
    display: flex;
    flex-direction: column;
  }

  textarea {
    min-height: 150px;
    margin-bottom: 18px;
  }

  button {
    min-width: 49%;
    margin-right: 15px;
  }

  small {
    max-width: 260px;
    margin-right: auto;

    font-size: 12px;
    line-height: 16px;
    color: $color-matterhorn;
  }

  a {
    color: $color-dodger-blue;
  }

  h2 {
    margin-top: -4px;
    margin-bottom: 36px;
    letter-spacing: 0.2px;
  }
}

.feedback__input-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  .form__item {
    flex-basis: 49.03%;
  }
}

.feedback__picture {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-grow: 1;
  margin-right: 6%;

  img {
    width: 150px;
    height: 150px;
    max-width: none;

    border-radius: 50%;
    object-fit: cover;
  }

  b {
    margin-top: 24px;

    font-size: 18px;
  }

  p {
    max-width: 150px;
    margin-top: 7px;
    margin-bottom: 3px;
  }
}
