.choose-garantee {
  padding-top: 100px;
}

.choose-garantee__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 51px;

  h2 {
    max-width: 384px;
    margin: 0;

    font-weight: 800;
    font-size: 34px;
    line-height: 41px;
  }

  p {
    max-width: 464px;
  }
}

.choose-garantee__picture {
  width: 533px;
  height: 364px;
  flex-shrink: 0;

  border-radius: 10px;

  overflow: hidden;

  @include viewport--xl {
    width: 27.76vw;
    height: 18.96vw;
  }

  img {
    width: 100%;
    height: 100%;
    max-width: none;

    object-fit: cover;
  }
}

.choose-garantee__inner-wrapper {
  max-width: 505px;
  width: 60%;
  margin-right: 2.3%;
  margin-left: 8%;
}

.choose-garantee__btns {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 28px;
  margin-bottom: 18px;

  button {
    margin-top: 5px;
    margin-bottom: 5px;
  }

  button:first-of-type {
    margin-right: 10px;
  }
}
