.footer-nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  border-top: 1px solid $color-solitude;
  border-bottom: 1px solid $color-solitude;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  a {
    display: inline-block;
    margin-bottom: 16px;
  }
}

.footer-nav__menu {
  a {
    line-height: 21px;
    color: $color-raven;
  }
}

.footer-nav__link {
  a {
    font-weight: 600;
    font-size: 18px;
    line-height: 23px;
    color: $color-nero;

    border-bottom: 1px solid $color-solitude;
  }
}

.footer-nav__more-link {
  a {
    font-weight: 600;
    color: $color-alizarin;
  }
}

.footer-nav__menu--submenu {
  display: flex;
  justify-content: space-between;

  &:first-of-type {
    max-width: 328px;
    flex-grow: 1;
  }
}

.footer-nav__menu--short {
  columns: 2 auto;
  flex-grow: 1;
  max-width: 350px;
  margin-left: 6.7%;
  margin-right: 1%;

  a {
    margin-bottom: 28px;
  }
}

.footer-nav__socials {
  margin-left: 3.5%;

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;

    background-color: $color-ghost-white;
    border-radius: 5px;
  }
}
