.problems-solved {
  padding-top: 75px;
  padding-bottom: 103px;

  background-color: $color-ghost-white;

  ul {
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    margin-top: 34px;

    list-style: none;
  }
}


.problems-solved__card {
  position: relative;

  display: flex;
  width: 380px;
  height: 278px;
  max-width: 26.57vw;
  max-height: 20.14vw;

  border-radius: 10px;
}

.problems-solved__picture {
  height: 100%;
  width: 100%;

  border-radius: 10px;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    max-width: none;

    object-fit: cover;
  }
}

.problems-solved__text {
  position: absolute;
  left: 0;
  bottom: 14%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 89.47%;
  min-height: 74px;
  padding: 2% 5%;

  background-color: $color-default-white;
  border-radius: 0 10px 10px 0;

  transition: top $default-transition-settings,
    width $default-transition-settings,
    height $default-transition-settings;

  p {
    margin: 0;
  }
}

.problems-solved__text-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;

  font-weight: 600;
  font-size: 20px;
  line-height: 24px;

  svg {
    width: 40px;
    max-width: 13.4%;
    margin-left: 4px;
    margin-bottom: 5px;
    flex-shrink: 0;
  }
}

.problems-solved__description {
  display: none;

  span {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 112px;
    min-width: max-content;
    height: 34px;
    padding: 5px 10px;
    margin-top: 9px;
    margin-bottom: 9px;

    color: $color-alizarin;
    font-weight: 600;

    background-color: $color-ghost-white;
    border-radius: 5px;

    &::before {
      content: "";
      width: 18px;
      height: 100%;

      background: url("../img/svg/mark.svg") center center no-repeat;
    }
  }
}

.problems-solved__card:hover {
  .problems-solved__text-header {
    align-self: flex-start;
    max-width: 305px;
  }

  .problems-solved__picture {
    border-radius: 12px 12px 10px 10px;
  }

  .problems-solved__text {
    top: 0;
    bottom: auto;

    width: 100%;
    min-height: 80.5%;
    border-radius: 10px;
  }

  .problems-solved__description {
    display: initial;
  }
}

