.promo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 26px;
  margin-bottom: 102px;

  h1 {
    margin: 0;

    font-weight: 800;
    font-size: 34px;
    line-height: 41px;
  }
}

.promo__inner-wrapper {
  max-width: 477px;

  a {
    margin-top: 33px;
  }
}

.promo__picture {
  width: 600px;
  height: 364px;

  border-radius: 10px;

  overflow: hidden;

  @include viewport--xl {
    width: 31.25vw;
    height: 18.96vw;
  }

  img {
    width: 100%;
    height: 100%;
    max-width: none;

    object-fit: cover;
  }
}
