
.tooltip {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 50px;
  height: 50px;

  background-color: $color-ghost-white;
  border-radius: 12px;
}

.tooltip .tooltiptext {
  position: absolute;
  bottom: -4px;
  left: 55px;
  width: 203px;
  padding: 10px 15px;

  font-size: 14px;
  line-height: 18px;
  text-align: left;
  color: $color-nero;
  background-color: $color-default-white;
  border-radius: 10px;
  box-shadow: $box-shadow-black;

  visibility: hidden;
  z-index: 1;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 100%;
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent $color-default-white transparent transparent;
  }
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

