.user-nav {
  display: flex;
  align-items: center;
  min-height: 106px;
  width: 100%;

  border-top: 1px solid $color-ghost-white;
  border-bottom: 1px solid $color-ghost-white;
}

.user-nav__search-form {
  position: relative;

  input {
    width: 567px;
    height: 60px;
    padding: 20px 59px;

    font-size: 14px;
    line-height: 18px;
    color: $color-manatee;
    background-color: $color-ghost-white;
    border-radius: 10px;
  }

  button {
    position: absolute;
    top: 17px;
    left: 20px;
  }
}

.user-nav__phone-desc {
  margin: 0;
  padding: 0;

  font-size: 14px;
  line-height: 18px;
  color: $color-raven;
}

.user-nav__phone-link {
  font-weight: 600;
  font-size: 20px;
  line-height: 25px;
}

.user-nav__login-btn {
  flex-direction: row;
  justify-content: space-between;
  padding: 0 20px;

  font-weight: 400;
  font-size: 14px;
  line-height: 18px;

  border: 1px solid $color-solitude;

  span {
    margin-left: 15px;
  }
}
