.breadcrumbs {
  ul {
    display: flex;
    align-items: center;

    list-style: none;
    padding: 0;
    margin: 0;
  }
}

.breadcrumbs__link {
  display: flex;
  align-items: center;

  color: $color-raven;
  font-size: 14px;
  line-height: 18px;

  &::after {
    content: "";
    width: 16px;
    height: 22px;
    margin-left: 8px;
    margin-right: 5px;

    background: url("../img/svg/breadcrumbs-arrow.svg") center center no-repeat;
  }

  &--current {
    &::after {
      content: none;
    }
  }
}
