.dropdown__icons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 20px;
  padding-top: 27px;

  border-top: 1px solid $color-solitude;
}

.dropdown__icon-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 262px;
  margin-bottom: 20px;
}

.dropdown__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
}

.dropdown__icon-text {
  width: 179px;
  margin: 0;
  padding: 0;

  font-size: 14px;
  line-height: 18px;
}

.dropdown__table {
  margin-top: 18px;

  font-size: 14px;
  line-height: 18px;
}

.dropdown__table-tr {
  display: flex;
  margin-bottom: 18px;
  padding-bottom: 6px;

  border-bottom: 1px solid $color-solitude;

  &--no-border {
    border: none;
  }

  &--margin {
    margin-bottom: 5px;
  }
}

.dropdown__table-td-title {
  display: flex;
  width: 447px;
  padding-right: 85px;

  font-weight: 600;

  &--light {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
  }
}

.dropdown__table-td-data {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 7px;

  span {
    margin-right: 5px;
  }
}
