.form__item {
  input,
  textarea {
    padding: 19px 20px;
    width: 100%;

    border: 1px solid $color-solitude;
    border-radius: 10px;

    &::placeholder {
      color: $color-manatee;
      font-family: inherit;
      font-size: inherit;
    }
  }

  input {
    height: 60px;
  }
}

.form__btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
