.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;

  clip: rect(0 0 0 0);
}

.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  // c этим нет резины :(
  // min-width: 1360px;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;

  @include viewport--md {
    min-width: 0;
  }

  &--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.scroll-lock {
  position: fixed;

  overflow-y: auto;
}

.btn-reset {
  padding: 0;

  background: $color-transparent;
  border: none;
  cursor: pointer;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 60px;
  width: max-content;
  padding: 19px 40px;

  font-weight: 600;
  border-radius: 10px;
}

.btn-white {
  border: 1px solid $color-alizarin;
  color: $color-alizarin;
}

.btn-red {
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  color: $color-alizarin;
  border-bottom: 1px dashed $color-alizarin-2;
}

.btn-grey {
  width: 50px;
  height: 50px;

  background-color: $color-ghost-white;
  border-radius: 12px;
}

.btn-accented {
  background-color: $color-alizarin;
  color: $color-default-white;
}

.btn-icon {
  height: 40px;
  min-width: 40px;
  width: max-content;
  padding: 0;

  border-radius: 5px;
  background-color: $color-ghost-white;
}

.clip {
  max-width: 112px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0;
  }

  100% {
    visibility: hidden;
    opacity: 0;
  }
}
