.reviews {
  padding-top: 73px;
  padding-bottom: 90px;

  background-color: $color-ghost-white;

  h2 {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
  }
}

.reviews__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.reviews__slide {
  display: flex;
  justify-content: space-between;
  padding: 40px;

  background-color: $color-default-white;
  border-radius: 10px;
}

.reviews__logo {
  display: flex;
  width: 80px;
  height: 80px;

  border: 1px solid $color-solitude;
  border-radius: 10px;

  img {
    object-fit: contain;
    max-height: 100%;
    margin: auto;
  }
}

.reviews__inner-wrapper {
  cite {
    display: flex;
    flex-direction: column;
    margin-top: 30px;

    font-style: normal;
    font-weight: 600;
    font-size: 20px;
  }

  span {
    margin-top: 4px;

    font-weight: 400;
    font-size: 16px;
  }

  blockquote {
    margin: 23px 15% 0 0;
  }
}

.reviews__picture {
  width: 437px;
  height: 285px;
  flex-shrink: 0;

  border-radius: 10px;

  overflow: hidden;

  @include viewport--xl {
    width: 22.76vw;
    height: 14.84vw;
  }

  img {
    width: 100%;
    height: 100%;
    max-width: none;

    object-fit: cover;
  }
}

.reviews__slider {
  position: relative;
}
