.user-widget {
  display: flex;
  align-items: center;
  min-height: 50px;
  width: 100%;

  background-color: $color-ghost-white;
}

.user-widget__list {
  display: flex;
  flex-direction: row;

  list-style: none;
  margin: 0;
  padding: 0;
}

.user-widget__item {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  margin-right: 50px;
}

.user-widget__link {
  margin-left: 7px;
}

.user-widget__desc {
  margin: 0;
  padding: 0;

  font-size: 14px;
  line-height: 18px;
  color: $color-raven;
}

.user-widget__meta {
  display: flex;
  flex-direction: row;
}

.user-widget__meta-link {
  margin: 0;
  padding: 0;

  font-weight: 600;
  font-size: 16px;
  line-height: 21px;
  color: $color-alizarin;
}
