.guarantee__title {
  margin: 0;
  padding: 0;
}

.guarantee__list {
  margin: 0;
  margin-top: 32px;
  padding: 0;
  list-style: none;
}

.guarantee__item {
  position: relative;
  max-width: 1200px;
  width: 100%;
  margin-bottom: 17px;
  padding: 25px 30px;
  padding-bottom: 0;

  border: 1px solid $color-solitude;
  border-radius: 10px;

  .guarantee__item-col--pdf {
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200px;
    height: 100%;

    background-color: $color-ghost-white;

    img {
      width: 120px;
      height: 162px;
    }
  }

  .guarantee__item-col--pdf-bottom {
    bottom: 0;
    right: 0;
    width: 302px;
    height: 321px;

    img {
      width: 181px;
      height: 244px;
    }
  }

  .guarantee__btn-get--right {
    position: absolute;
    top: 0;
    right: 20px;
    width: 230px;
    margin-top: 30px;
  }
}

.guarantee__content {
  display: flex;
  flex-direction: row;
  padding-bottom: 30px;
}

.guarantee__item-col {
  /* 1200px - 60px = 1140px
  base - 268px = 23.5%
  table - 431px = 37.8%
  icons - 272px = 23.6% */

  &--base {
    width: 23.5%;
    padding-right: 20px;
  }

  &--table {
    width: 37.8%;
    padding-right: 20px;
  }

  &--icons {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 23.6%;
    padding-left: 50px;

    border-left: 1px solid $color-solitude;
  }

  &--indent {
    width: 15.1%;
  }
}

.guarantee__item-title {
  margin: 0;
  padding: 0;

  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
}

.guarantee__item-title-desc {
  width: max-content;
  margin: 0;
  margin-top: 10px;
  padding: 4px 9px;

  font-size: 14px;
  line-height: 18px;
  color: $color-default-white;
  text-align: center;

  background: $color-cello;
  border-radius: 5px;
}

.guarantee__btn-get {
  max-width: 226px;
  width: 100%;
  margin: 0;
  margin-top: 40px;
  padding: 19px 0;
}

.guarantee__item-desc {
  width: 382px;
  margin: 0;
  padding: 0;

  font-size: 16px;
  line-height: 21px;

  @include viewport--xxl {
    width: 100%;
  }
}

.guarantee__table {
  margin-top: 20px;

  font-size: 14px;
  line-height: 18px;
}

.guarantee__table-title {
  width: 144px;
  font-weight: 600;
}

.guarantee__btn-more-details {
  margin-top: 10px;
}

.guarantee__btn-more-details-text {
  margin-right: 3px;
}

.guarantee__icon-up svg {
  transform: rotate(180deg);
}

.guarantee__tooltip {
  margin-right: 15px;
  margin-bottom: 15px;
}

.guarantee__more-pdf-link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    z-index: 1;
  }

  .guarantee__more-pdf {
    position: absolute;
    bottom: 5px;
    margin: 0 auto;

    font-weight: 600;
    font-size: 14px;
    line-height: 15px;
    color: $color-alizarin;
    z-index: 2;
  }
}
