.footer {
  border-top: 1px solid $color-solitude;
}

.footer__contact-us,
.footer__contact-btns,
.footer__copyright {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.footer__contact-us {
  padding-top: 34px;
  padding-bottom: 34px;

  p {
    flex-basis: 30%;
    flex-grow: 1;

    font-size: 18px;
    line-height: 27px;
  }
}

.footer__contact-phone {
  flex-basis: 156px;
  display: flex;
  flex-direction: column;
  margin-right: 3.5%;
  margin-left: 3.5%;

  color: $color-raven;
  font-size: 14px;
  line-height: 18px;

  a {
    color: $color-nero;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
  }
}

.footer__contact-btns {
  flex-basis: 395px;
  margin-left: auto;

  a {
    padding-right: 22.5px;
    padding-left: 22.5px;
    margin-top: 5px;
    margin-bottom: 5px;

    &:first-of-type {
      min-width: 225px;
    }
  }
}

.footer__nav {
  padding-top: 44px;
  padding-bottom: 31px;
}

.footer__copyright {
  padding-top: 30px;
  padding-bottom: 30px;

  font-size: 14px;
  line-height: 18px;

  a {
    margin-left: 32px;
    color: $color-dodger-blue;
  }
}

.footer__designed-by {
  display: flex;
  align-items: center;
  padding: 13px 32px;
  margin-left: auto;

  background: $color-ghost-white;
  border-radius: 10px;

  a {
    margin-right: 0;
    margin-left: 7px;
  }
}
