.main-nav {
  display: flex;
  align-items: center;
  min-height: 53px;

  border-top: 1px solid $color-ghost-white;
  border-bottom: 1px solid $color-ghost-white;
}

.main-nav__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}
